import addresses from 'config/constants/contracts';
var chainId = process.env.REACT_APP_CHAIN_ID;
export var getCakeAddress = function () {
    return addresses.cake[chainId];
};
export var getMasterChefAddress = function () {
    return addresses.masterChef[chainId];
};
export var getMulticallAddress = function () {
    return addresses.mulltiCall[chainId];
};
export var getWbnbAddress = function () {
    return addresses.wbnb[chainId];
};
export var getLotteryAddress = function () {
    return addresses.lottery[chainId];
};
export var getLotteryTicketAddress = function () {
    return addresses.lotteryNFT[chainId];
};
