import BigNumber from 'bignumber.js';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import useRefresh from 'hooks/useRefresh';
import { fetchFarmsPublicDataAsync, fetchPoolsUserDataAsync } from './actions';
import { QuoteToken } from '../config/constants/types';
import { usersBlacklist } from '../config/constants';
var ZERO = new BigNumber(0);
export var useFetchPublicData = function () {
    var dispatch = useDispatch();
    var slowRefresh = useRefresh().slowRefresh;
    useEffect(function () {
        dispatch(fetchFarmsPublicDataAsync());
        // dispatch(fetchPoolsPublicDataAsync())
    }, [dispatch, slowRefresh]);
};
// Farms
export var useFarms = function () {
    var farms = useSelector(function (state) { return state.farms.data; });
    return farms;
};
export var useFarmFromPid = function (pid) {
    var farm = useSelector(function (state) { return state.farms.data.find(function (f) { return f.pid === pid; }); });
    return farm;
};
export var useFarmFromSymbol = function (lpSymbol) {
    var farm = useSelector(function (state) { return state.farms.data.find(function (f) { return f.lpSymbol === lpSymbol; }); });
    return farm;
};
export var useFarmUser = function (pid, account) {
    var farm = useFarmFromPid(pid);
    if (usersBlacklist.includes(account)) {
        return {
            allowance: new BigNumber(0),
            tokenBalance: new BigNumber(0),
            stakedBalance: new BigNumber(0),
            earnings: new BigNumber(0),
        };
    }
    return {
        allowance: farm.userData ? new BigNumber(farm.userData.allowance) : new BigNumber(0),
        tokenBalance: farm.userData ? new BigNumber(farm.userData.tokenBalance) : new BigNumber(0),
        stakedBalance: farm.userData ? new BigNumber(farm.userData.stakedBalance) : new BigNumber(0),
        earnings: farm.userData ? new BigNumber(farm.userData.earnings) : new BigNumber(0),
    };
};
// Pools
export var usePools = function (account) {
    var fastRefresh = useRefresh().fastRefresh;
    var dispatch = useDispatch();
    useEffect(function () {
        if (account) {
            dispatch(fetchPoolsUserDataAsync(account));
        }
    }, [account, dispatch, fastRefresh]);
    var pools = useSelector(function (state) { return state.pools.data; });
    return pools;
};
export var usePoolFromPid = function (sousId) {
    var pool = useSelector(function (state) { return state.pools.data.find(function (p) { return p.sousId === sousId; }); });
    return pool;
};
// Prices
export var usePriceBnbBusd = function () {
    var pid = 2; // BUSD-BNB LP
    var farm = useFarmFromPid(pid);
    return farm.tokenPriceVsQuote ? new BigNumber(farm.tokenPriceVsQuote) : ZERO;
};
export var usePriceCakeBusd = function () {
    // const pid = 1 // CAKE-BNB LP
    // const bnbPriceUSD = usePriceBnbBusd()
    // const farm = useFarmFromPid(pid)
    // return farm.tokenPriceVsQuote ? bnbPriceUSD.times(farm.tokenPriceVsQuote) : ZERO
    var pid = 0; // VEGAN-BUSD LP
    var farm = useFarmFromPid(pid);
    return farm.tokenPriceVsQuote ? new BigNumber(farm.tokenPriceVsQuote) : ZERO;
};
export var useTotalValue = function () {
    var farms = useFarms();
    var bnbPrice = usePriceBnbBusd();
    var cakePrice = usePriceCakeBusd();
    var value = new BigNumber(0);
    for (var i = 0; i < farms.length; i++) {
        var farm = farms[i];
        if (farm.lpTotalInQuoteToken) {
            var val = void 0;
            if (farm.quoteTokenSymbol === QuoteToken.BNB) {
                val = bnbPrice.times(farm.lpTotalInQuoteToken);
            }
            else if (farm.quoteTokenSymbol === QuoteToken.CAKE) {
                val = cakePrice.times(farm.lpTotalInQuoteToken);
            }
            else {
                val = farm.lpTotalInQuoteToken;
            }
            value = value.plus(val);
        }
    }
    return value;
};
