import { useContext } from 'react';
import { TranslationsContext } from '../contexts/Localisation/translationsContext';
var variableRegex = /%(.*?)%/;
var replaceDynamicString = function (foundTranslation, fallback) {
    var stringToReplace = variableRegex.exec(foundTranslation)[0];
    // const indexToReplace = foundTranslation.split(' ').indexOf(stringToReplace)
    var fallbackValueAtIndex = fallback.split(' ')[0];
    return foundTranslation.replace(stringToReplace, fallbackValueAtIndex);
};
export var getTranslation = function (translations, translationId, fallback) {
    var foundTranslation = translations.find(function (translation) {
        return translation.data.stringId === translationId;
    });
    if (foundTranslation) {
        var translatedString = foundTranslation.data.text;
        var includesVariable = translatedString.includes('%');
        if (includesVariable) {
            return replaceDynamicString(translatedString, fallback);
        }
        return translatedString;
    }
    return fallback;
};
// TODO: Replace instances where this is called directly with the "useI18n" hook.
// Using this directly can lead to errors because "useContext" is not preserved between renders
// @see https://reactjs.org/docs/hooks-rules.html
export var TranslateString = function (translationId, fallback) {
    var translations = useContext(TranslationsContext).translations;
    if (translations[0] === 'error') {
        return fallback;
    }
    if (translations.length > 0) {
        return getTranslation(translations, translationId, fallback);
    }
    return fallback;
};
