import Web3 from 'web3';
import getRpcUrl from 'utils/getRpcUrl';
var RPC_URL = getRpcUrl();
var httpProvider = new Web3.providers.HttpProvider(RPC_URL, { timeout: 10000 });
/**
 * Provides a web3 instance using our own private provider httpProver
 */
var getWeb3 = function () {
    var web3 = new Web3(httpProvider);
    return web3;
};
var getContract = function (abi, address, contractOptions) {
    var web3 = getWeb3();
    return new web3.eth.Contract(abi, address, contractOptions);
};
export { getWeb3, getContract, httpProvider };
